function SlydeCard({ slyde, index, isSelected, onClick }: {
  slyde: Slyde
  index: number
  isSelected: boolean
  onClick: () => void
}) {
  const typeInfo = SLYDE_TYPE_INFO[slyde.type]
  
  return (
    <motion.button
      onClick={onClick}
      className={`w-full text-left rounded-xl transition-all duration-200 group ${
        isSelected
          ? 'bg-red-600 shadow-lg shadow-red-600/20 scale-[1.02]'
          : 'bg-white hover:bg-gray-50 border border-gray-200 hover:border-gray-300'
      }`}
      whileHover={{ scale: isSelected ? 1.02 : 1.01 }}
      whileTap={{ scale: 0.98 }}
    >
      <div className="p-3 flex gap-3">
        {/* Drag handle */}
        <div className={`flex flex-col justify-center gap-0.5 ${isSelected ? 'text-white/60' : 'text-gray-300'}`}>
          <div className="flex gap-0.5">
            <div className="w-1 h-1 rounded-full bg-current" />
            <div className="w-1 h-1 rounded-full bg-current" />
          </div>
          <div className="flex gap-0.5">
            <div className="w-1 h-1 rounded-full bg-current" />
            <div className="w-1 h-1 rounded-full bg-current" />
          </div>
          <div className="flex gap-0.5">
            <div className="w-1 h-1 rounded-full bg-current" />
            <div className="w-1 h-1 rounded-full bg-current" />
          </div>
        </div>
        
        {/* Thumbnail */}
        <div className="w-12 h-20 rounded-lg overflow-hidden bg-gray-200 flex-shrink-0 relative">
          <video
            src={slyde.mediaSrc}
            muted
            playsInline
            className="w-full h-full object-cover"
          />
          <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent" />
          <div className="absolute bottom-1 left-1 text-[8px] font-bold text-white bg-black/50 px-1 rounded">
            {typeInfo.icon}
          </div>
        </div>
        
        {/* Info */}
        <div className="flex-1 min-w-0 py-1">
          <div className={`text-xs font-medium mb-0.5 ${isSelected ? 'text-white/70' : 'text-gray-400'}`}>
            {index + 1} ‚Ä¢ {typeInfo.label}
          </div>
          <div className={`font-semibold truncate ${isSelected ? 'text-white' : 'text-gray-900'}`}>
            {slyde.title}
          </div>
          <div className={`text-xs truncate ${isSelected ? 'text-white/70' : 'text-gray-500'}`}>
            {slyde.subtitle}
          </div>
        </div>
        
        {/* Arrow */}
        <div className={`flex items-center ${isSelected ? 'text-white/60' : 'text-gray-300'}`}>
          <svg className="w-4 h-4" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 5l7 7-7 7" />
          </svg>
        </div>
      </div>
    </motion.button>
  )
}

// ============================================
// SLYDE TYPE PICKER MODAL
// ============================================

function SlydeTypePicker({ isOpen, onClose, onSelect }: {
  isOpen: boolean
  onClose: () => void
  onSelect: (type: SlydeType) => void
}) {
  if (!isOpen) return null
  
  return (
    <motion.div
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      exit={{ opacity: 0 }}
      className="fixed inset-0 bg-black/40 backdrop-blur-sm z-50 flex items-center justify-center p-4"
      onClick={onClose}
    >
      <motion.div
        initial={{ scale: 0.95, opacity: 0 }}
        animate={{ scale: 1, opacity: 1 }}
        exit={{ scale: 0.95, opacity: 0 }}
        className="bg-white rounded-2xl border border-gray-200 p-6 max-w-md w-full shadow-2xl"
        onClick={(e) => e.stopPropagation()}
      >
        <div className="flex items-center justify-between mb-6">
          <h3 className="text-xl font-bold text-gray-900">Add Slyde</h3>
          <button onClick={onClose} className="text-gray-400 hover:text-gray-600">
            <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
            </svg>
          </button>
        </div>
        
        <div className="grid grid-cols-3 gap-3">
          {(Object.entries(SLYDE_TYPE_INFO) as [SlydeType, typeof SLYDE_TYPE_INFO[SlydeType]][]).map(([type, info]) => (
            <button
              key={type}
              onClick={() => {
                onSelect(type)
                onClose()
              }}
              className="bg-gray-50 hover:bg-gray-100 border border-gray-200 hover:border-red-300 rounded-xl p-4 text-center transition-all group"
            >
              <div className="text-3xl mb-2 group-hover:scale-110 transition-transform">{info.icon}</div>
              <div className="font-semibold text-gray-900 text-sm">{info.label}</div>
              <div className="text-gray-500 text-xs mt-1">{info.description}</div>
            </button>
          ))}
        </div>
      </motion.div>
    </motion.div>
  )
}

